import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Row from "react-bootstrap/es/Row";
import Button from "react-bootstrap/es/Button";
import Col from "react-bootstrap/es/Col";
import Container from "react-bootstrap/es/Container";
import Table from "react-bootstrap/es/Table";
import ProgressBar from "react-bootstrap/es/ProgressBar";
import request from "request-promise-native";
import Modal from "react-responsive-modal";
import QRCode from "qrcode.react";
import Sound from 'react-sound';
import DashCore from '@dashevo/dashcore-lib'
import {optionalCallExpression} from "@babel/types";

const failSound = './fail-sound-effect.mp3';
const successSound = './sound-effect-tada.mp3';
const transactionSound = './txIn.mp3';
const clickSound = './clickSound.wav';

const width = 9;
const height = 5;

const callUrl = "http://192.168.1.102/process_donation.bin";
const dashAddress = "Xbkt2mnNaKPQw1XEZknfTsnvN8bjZTgTHm";
const label = "Dash+For+Beer";
const dashGetRateUrl = "https://rates2.dashretail.org/rates?source=dashretail&symbol=dasheur";
const sendTxUrl = "https://insight.dash.org/insight-api/tx/send";
const getTxUrl = "https://insight.dash.org/insight-api/tx/";

const baseTxCheck = "https://blockchair.com/dash/transaction/";

const beerPrice = 0.01;

const eventToListenTo = 'txlock';
const room = 'inv';

const scanQrTimeout = 60;

const modalStyle = {
    overlay: {
        transform: "rotate(90deg)",
        transformOrigin:"bottom left",
        position:"absolute",
        top:"-100vw",

        height:"100vw",
        width:"100vh",
    },
    modal: {
        minWidth: '300px',
        width: '500px'
    }
};

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
        loading: false,
        success: null,
        failed: null,
        dashQrCodeDisplay: null,
        dashAm: null,
        playSuccess: false,
        playFailed: false,
        playTx: false,
        playClick: false,
    }
    this.generated = this.generateEverything(height,width);

    this.address = window.localStorage.getItem("address");
    this.privKey = window.localStorage.getItem("privKey")!=null ? new DashCore.PrivateKey(window.localStorage.getItem("privKey")) : null;

    if(this.address==null || this.privKey==null) {
        //Generate a new one
        this.privKey = new DashCore.PrivateKey();
        this.address = this.privKey.toAddress();

        window.localStorage.setItem("address",this.address.toString());
        window.localStorage.setItem("privKey",this.privKey.toString());
    }

  }

  generateEverything(totalRows, totalColumns) {
    const table = [];
    for(let i=0;i<totalRows;i++) {
        if(i==0) {
            table.push(this.generateFirstRow(i, totalColumns));
        } else {
            table.push(this.generateRow(i, totalColumns));
        }
    }
    return (
        <Table style={{
            height: "100%"
        }}>
          <tbody>
            {table}
          </tbody>
        </Table>
    )
  }

  generateFirstRow(rowNumber, totalCells) {
      const table = [];
      table.push(this.generateCell(rowNumber, -1, 2));
      table.push(this.generateCell(rowNumber, 1, 2));
      table.push(this.generateCell(rowNumber, 3, 1));
      table.push(this.generateCell(rowNumber, 5, 2));
      table.push(this.generateCell(rowNumber, 7, 2));
      return (
          <tr key={"row"+rowNumber}>
              {table}
          </tr>
      )
  }

  generateRow(rowNumber, totalCells) {
    const table = [];
    for(let i=0;i<totalCells;i++) {
      table.push(this.generateCell(rowNumber, i, 1));
    }
    return (
      <tr key={"row"+rowNumber}>
        {table}
      </tr>
    );
  }

  generateCell(rowNumber, columnNumber, collSpan) {
    return (
        <th key={"cell"+rowNumber+"-"+columnNumber} colSpan={collSpan}>
          <Button variant="light" onClick={() => {
            this.buttonClicked.bind(this)(rowNumber, columnNumber);
          }} style={{
              height: "140px"
          }}>
            <img src="https://morebeer-web-8-pavinthewaysoftw.netdna-ssl.com/product_image/morebeer/500x500/27498.png" style={{
              width: "100%",
              height: "100%",
                objectFit: "contain"
            }}/>
          </Button>
            <p style={{
                textAlign: "center"
            }}>{beerPrice} â‚¬</p>
        </th>
    );
  }

  async txReceived(txId, desired, x, y) {
      const options = {
          method: 'GET',
          uri: getTxUrl+txId,
          json: true,
          resolveWithFullResponse: true,
          simple: false
      };

      const httpReqTx = await request(options);

      if(httpReqTx.statusCode!=200) {
          //failed
          return;
      }

      let index = -1;
      for(const outTx of httpReqTx.body.vout) {
          if(outTx.scriptPubKey!=null && outTx.scriptPubKey.addresses!=null) {
              for(const address of outTx.scriptPubKey.addresses) {
                  if(address==this.address) {
                      //Found!
                      index = outTx.n;
                      break;
                  }
              }
          }
      }

      if(index===-1) {
          //Error
          return;
      }

      console.log("Response from GET: ",httpReqTx.body);

      console.log("PAID!");
      this.lastTx = {
          txId: txId,
          amount: desired,
          outputIndex: index,
          scriptHex: httpReqTx.body.vout[index].scriptPubKey.hex,
          fromAddress: httpReqTx.body.vin[0].addr
      };
      this.sendRequest(x, y);
      this.cancelTransaction();
      this.setState({
          playTx: true,
          loading: true,
          success: null,
          failed: null
      });
  }

  async buttonClicked(x, y) {
      if(this.state.loading) return;
      console.log("Clicked "+x+" "+y);
      this.setState({
          playClick: true
      })
      const qrCode = await this.getDashQrCode(beerPrice);
      if(qrCode==null) {
          this.setState({
              loading: false,
              success: null,
              failed: "Cannot fetch the exchange rate of DASH :(",
              dashQrCodeDisplay: null
          });
          return;
      }

      this.socketInterval = setInterval(() => {
          this.cancelTransaction();
      },scanQrTimeout*1000);

      this.socket = window.io("http://insight.dash.org/");
      this.socket.on('connect', function() {
          // Join the room.
          console.log("Connected to DASH network");
          this.socket.emit('subscribe', room);
      }.bind(this))
      this.socket.on(eventToListenTo, function(data) {
          if (data.txlock) {
              console.log("New InstantSend transaction received: ", data)
              if(data.vout!=null) {
                  for(const outTx of data.vout) {
                      const desired = Math.round(qrCode.amount*100000000);
                      console.log("OUT TX: ", outTx[dashAddress]);
                      console.log("Desired TX: ", desired);
                      if(outTx[this.address]===desired) {
                          this.txReceived(data.txid, desired, x , y);
                          return;
                      }
                  }
              }
          } else {
              console.log("New transaction received: ", data)
          }

      }.bind(this))

      this.setState({
          loading: false,
          success: null,
          failed: null,
          dashQrCodeDisplay: qrCode.qrCode,
          dashAm: qrCode.amount
      })

      //this.sendRequest(x,y);
      /*this.setState({
        loading: true,
        success: null,
        failed: null
      })*/
  }

  async getDashQrCode(eur) {
        const amount = await this.getDashForEuro(eur);
        if(amount==null) {
            return null;
        }
        return {
            qrCode: "dash:"+this.address.toString()+"?amount="+amount+"&label="+label,
            amount: amount
        };
  }

  cancelTransaction() {
        this.setState({
            dashQrCodeDisplay: null
        });
        this.socket.disconnect();
        clearInterval(this.socketInterval);
        //console.log();
  }

  async getDashForEuro(eur) {
      const options = {
          method: 'GET',
          uri: dashGetRateUrl,
          json: true,
          resolveWithFullResponse: true,
          simple: false
      };

      const response = await request(options);

      console.log("response body: ", response.body);

      if(response.statusCode!=200 || response.body==null || response.body.length==0 || response.body[0]==null || response.body[0].price==null) {
          console.log("Error fetching exchange rate of DASH");
          return null;
      }

      return Math.floor(eur/parseFloat(response.body[0].price)*100000000)/100000000;

  }

  async vendSuccess() {
      if(this.lastTx!=null) {
          //Send the transaction
          const utxo = {
              "txId" : this.lastTx.txId,
              "outputIndex" : this.lastTx.outputIndex,
              "address" : this.address,
              "script" : this.lastTx.scriptHex,
              "satoshis" : this.lastTx.amount
          };

          console.log("UTXO created: ",utxo);

          const transaction = new DashCore.Transaction()
              .from(utxo)
              .to(dashAddress, this.lastTx.amount-200)
              .sign(this.privKey)
              .fee(200);

          const serializedTx = transaction.serialize();

          const options = {
              method: 'POST',
              uri: sendTxUrl,
              body: {
                  rawtx: serializedTx
              },
              json: true,
              resolveWithFullResponse: true,
              simple: false
          };

          const response = await request(options);

          console.log("Response body: ", response.body);
          console.log("Status code: ", response.statusCode);

          if(response.statusCode===200) {
              //Success
          }
      }
      this.setState({
          playSuccess: true,
          loading: false,
          success: "Here's your beer :)"
      });
      return;
  }

  async vendFailed() {
      let refundTxId = null;
      if(this.lastTx!=null) {
          //Send the transaction
          const utxo = {
              "txId" : this.lastTx.txId,
              "outputIndex" : this.lastTx.outputIndex,
              "address" : this.address,
              "script" : this.lastTx.scriptHex,
              "satoshis" : this.lastTx.amount
          };

          console.log("UTXO created: ",utxo);

          const transaction = new DashCore.Transaction()
              .from(utxo)
              .to(this.lastTx.fromAddress, this.lastTx.amount-200)
              .sign(this.privKey)
              .fee(200);

          console.log("TX created");

          const serializedTx = transaction.toString();

          console.log("TX serialize: ",serializedTx);

          const options = {
              method: 'POST',
              uri: sendTxUrl,
              body: {
                  rawtx: serializedTx
              },
              json: true,
              resolveWithFullResponse: true,
              simple: false
          };

          const response = await request(options);

          console.log("Response body: ", response.body);
          console.log("Status code: ", response.statusCode);

          if(response.statusCode===200) {
              //Success
              refundTxId = response.body.txid;
          }
      }
      this.setState({
          playFailed: true,
          loading: false,
          failed: (
              <p>
                  We are really sorry, something didn't go well. Don't worry, your transaction has been refunded, you can check the status here:<br/>
                  <div style={{
                      textAlign: "center"
                  }}>
                      <QRCode  value={baseTxCheck+refundTxId} size={256}/>
                  </div>
              </p>
          )
      });
  }

  async sendRequest(x,y) {
    const options = {
        method: 'GET',
        uri: callUrl+"?x="+(y+1)+"&y="+(5-x),
        json: true,
        resolveWithFullResponse: true,
        simple: false,
        timeout: 10
    };

    const promise = request(options);
    const interval = setInterval(() => {
        promise.abort();
        this.vendFailed();
        clearInterval(interval);
    },10000);
    const response = await promise;
    clearInterval(interval);

    if(response.statusCode===200) {
        //Success
        //Split the newlines
        console.log(response.body);
        const arr = response.body.split("\n");
        if(arr.length>1) {
            //Ok
            console.log("ARR: "+arr[1]);
            const arr2 = arr[1].split(":");
            if(arr2.length>1) {
                console.log("ARR21: "+arr2[0]);
                console.log("ARR22: "+arr2[1]);
                if(arr2[0]=="message" && arr2[1]=="SUCCESS") {
                    //Success operation
                    this.vendSuccess();
                    return;
                }
            }
        }
        //Failed
        this.vendFailed();
        return;
    }
  }

  render() {
    return (
        <div className={"wrapper"}>
          <div style={{
                width: "100%",
              height: "100%"
          }}>

              <Sound
                  url={transactionSound}
                  playStatus={this.state.playTx ? Sound.status.PLAYING : Sound.status.STOPPED}
                  onFinishedPlaying={() => {
                      this.setState({
                          playTx: false
                      })
                  }}
              />
              <Sound
                  url={successSound}
                  playStatus={this.state.playSuccess ? Sound.status.PLAYING : Sound.status.STOPPED}
                  onFinishedPlaying={() => {
                      this.setState({
                          playSuccess: false
                      })
                  }}
              />
              <Sound
                  url={failSound}
                  playStatus={this.state.playFailed ? Sound.status.PLAYING : Sound.status.STOPPED}
                  onFinishedPlaying={() => {
                      this.setState({
                          playFailed: false
                      })
                  }}
              />
              <Sound
                  url={clickSound}
                  playStatus={this.state.playClick ? Sound.status.PLAYING : Sound.status.STOPPED}
                  onFinishedPlaying={() => {
                      this.setState({
                          playClick: false
                      })
                  }}
              />

              <Modal open={this.state.failed!=null}
                     styles={modalStyle}
                     center
                     onClose={()=>{this.setState({
                         failed: null
                     })}}
                     focusTrapped={false}>
                  <h2>Failed!</h2>
                  <p>{this.state.failed}</p>
              </Modal>

              <Modal open={this.state.success!=null}
                     styles={modalStyle}
                     center
                     onClose={()=>{this.setState({
                         success: null
                     })}}
                     focusTrapped={false}>
                  <h2>Success!</h2>
                  <p>{this.state.success}</p>
              </Modal>

              <Modal open={this.state.loading}
                   styles={modalStyle}
                   center
                     showCloseIcon={false}
                   onClose={()=>{}}
                   focusTrapped={false}>
                  <h2>Processing...</h2>
                  <p>Dispensing your beer, please wait! :)</p>
                  <ProgressBar animated  now={100}/>
              </Modal>

              <Modal open={this.state.dashQrCodeDisplay!=null}
                     styles={modalStyle}
                     center
                     onClose={this.cancelTransaction.bind(this)}
                     focusTrapped={false}>
                  <h2 style={{
                      textAlign: "center"
                  }}>Please scan the QR code!</h2>
                  <div style={{
                      textAlign: "center"
                  }}>
                      <QRCode value={this.state.dashQrCodeDisplay!=null ? this.state.dashQrCodeDisplay : ""} size={256} />
                  </div>
                  <h3 style={{
                      textAlign: "center"
                  }}>{this.state.dashAm} DASH</h3>
              </Modal>

            {this.generated}
          </div>
        </div>
    );
  }

}

export default App;
